import api from '../../api/api.js'
export default {
	data() {
		return {
			showLoadmore: false,
			isLoading: true,
			page: 1,
			leftList: [],
			rightList: []
		}
	},
	created: function() {
		uni.showLoading({
			title: "福利马上来~"
		})
		this.getGirls();
	},
	methods: {
		getGirls() {
			this.showLoadmore = true;
			api.get(`/data/福利/10/${this.page}`)
				.then(res => {
					uni.hideLoading();
					let leftList = [];
					let rightList = [];
					let data = res.results;
					for (let i = 0; i < data.length; i++) {
						var item = data[i];
						if (i % 2 == 0) {
							leftList.push(item);
						} else {
							rightList.push(item);
						}
					}
					this.leftList = this.page == 1 ? leftList : this.leftList.concat(leftList);
					this.rightList = this.page == 1 ? rightList : this.rightList.concat(rightList);
					if (data.length < 10) {
						this.isLoading = false;
					} else {
						this.showLoadmore = false;
						this.page++;
					}
				}).catch(e => {
					if (this.page == 1) {
						this.leftList = [];
						this.rightList = [];
					}
					this.isLoading = false;
				})
		},
		onLoadMore() {
			if (!this.showLoadmore && this.isLoading) {
				this.getGirls();
			}
		},
		onImgClick(event) {
			let url = event.currentTarget.dataset.url;
			uni.previewImage({
				urls: [url]
			})
		}
	}
}
